body {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  height: 100vh;
  background-color: #676464;
}
.wrapper {
  background-color: rgb(255, 149, 149);
  width: 500px;
  height: 600px;
}
.inputField {
  width: 50%;
  text-align: center;
  margin-top: 200px;
  margin-left: 120px;
  border: none;
  padding: 1.5em;
  font-size: 1em;
  border-radius: 10px;
}
.btn {
  background-color: #4caf50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin-top: 15px;
  margin-left: 190px;
}
.btn:hover {
  background-color: #043306;
  cursor: pointer;
}
.your {
  width: 22ch;
  animation: typing 2s steps(22), blink .5s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
  font-family: monospace;
  font-size: 2em;
}

@keyframes typing {
  from {
    width: 0
  }
}
    
@keyframes blink {
  50% {
    border-color: transparent
  }
}
.age {
  margin-top: 100px;
  margin-left: 250px;
}


.title{
    text-align: center;
    letter-spacing:.1rem;
    line-height: 1.2;
    background-color:darkgrey;
}